.profileContainer{
    display: flex;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    max-width: 100%;
    background-color: white;
}
.profileContainer > div{
    display: flex;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profileContainer > div:first-child > h1{
    color: rgba(0, 0, 0, .555);
    transform: translateX(-10vmax) translateY(-2vmax);
    font: 500 2.2vmax "Roboto";
}

.profileContainer > div:first-child > img {
    width: 20vmax;
    height: 20vmax;
    object-fit: cover;
    border-radius: 100%;
    transition: all .5s;
    object-position: top;
}

.profileContainer > div:first-child > img:hover{
    transform: scale(1.05);
}
.profileContainer > div:first-child > a{
    border: none;
    background-color: tomato;
    font: 400 1vmax "Roboto";
    color: white;
    text-decoration: none;
    padding: .5vmax;
    width: 30%;
    margin: 4vmax;
    text-align: center;
    transition: all .5s;
}

.profileContainer > div:first-child > a:hover{
    background-color: rgb(204, 78, 56);
}

.profileContainer > div:last-child {
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 5vmax;
    box-sizing: border-box;
}

.profileContainer > div:last-child > div > h4 {
    color: black;
    font: 400 1vmax "Roboto";
}
.profileContainer > div:last-child > div > p {
    color: rgba(0, 0, 0, .555);
    font: 400 1vmax cursive;
    margin: .2vmax;
}

.profileContainer > div:last-child > div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.profileContainer > div:last-child > div:last-child > a {
    border: none;
    background-color: rgb(68, 68, 68);
    font: 400 1vmax "Roboto";
    color: white;
    text-decoration: none;
    padding: .5vmax;
    text-align: center;
    transition: all .5s;
    margin: 1vmax 0;
}

.profileContainer > div:last-child > div:last-child > a:hover{
    background-color: rgb(31, 31, 31);
}

@media screen and (max-width: 600px) {
    .profileContainer{
        flex-direction: column;
    }    
    .profileContainer > div {
        margin-top: 5vmax;
    }
    .profileContainer > div:first-child > h1{
        font: 500 3.2vmax "Roboto";
        transform: translateY(-2vmax);
    }
    .profileContainer > div:first-child > a{
        font: 400 1.7vmax "Roboto";
        padding: 1vmax;
    }
    .profileContainer > div:last-child {
        text-align: center;
        align-items: center;
        margin-bottom: 10vmax;
    }
    .profileContainer > div:last-child > div > p{
        font: 400 3vmax cursive;
        margin: .5vmax 0;
    }

    .profileContainer > div:last-child > div:last-child > a {
    font: 400 2.3vmax "Roboto";
    padding: 1vmax;
    margin: 2vmax 0;
    
}
.profileContainer > div:last-child > div > h4 {
    color: black;
    font: 400 2.3vmax "Roboto";
}


}

